import React, { useState, useEffect } from "react";
import Axios from "axios";
import { ListGroup, ListGroupItem } from "reactstrap";

const Repos = ({ user }) => {
  const [repos, setRepos] = useState([]);

  const FetchRepos = async () => {
    try {
      const { data } = await Axios.get(user.repos_url);
      setRepos(data);
    } catch (error) {
      <h1>Could not fetch repositories</h1>;
    }
  };

  useEffect(() => {
    FetchRepos();
  }, [user.repos_url]);

  return (
    <ListGroup className="ml-28 ">
      <h1 className="text-3xl font-bold text-cyan-600 mb-5">Repositories: <br/></h1>
      {repos.map((repo) => (
        <ListGroupItem >
          <div className="text-2xl text-cyan-500">{repo.name}</div>
          <div className="text-black">{repo.description}</div>
        </ListGroupItem>
      ))}
    </ListGroup>
  );
};

export default Repos;
