import React from "react";

const UserCard = ({ user }) => {
  return (
    <div className="mt-4">
      <div>
        <img className="h-80 justify-center border rounded-full" src={user.avatar_url} alt="" />
        <h2 className="text-2xl text-cyan-500 mt-5">{user.name}</h2>
        <p className="w-80 text-xl ">{user.location}</p>
        <p className=" w-96 text-lg">{user.bio}</p>
      </div>
    </div>
  );
};

export default UserCard;
