import React, { useState } from "react";
import Axios from "axios";
import UserCard from "./UserCard";
import Repos from "./Repos";

const Home = () => {
  const [query, setQuery] = useState("");
  const [user, setUser] = useState(null);

  const FetchDetails = async () => {
    try {
      const { data } = await Axios.get(`https://api.github.com/users/${query}`);
      setUser(data);
    } catch (error) {
      <p>user not found</p>;
    }
  };

  return (
    <div className="place-items-center">
      <div className="pt-4 pl-4 mx-44">
        <input
          className="py-2 px-4 w-64"
          type="text"
          value={query}
          onChange={(i) => setQuery(i.target.value)}
          placeholder="Enter name"
        />
        <button
          onClick={FetchDetails}
          className="bg-cyan-500 hover:bg-cyan-700 place-items-center text-white font-bold py-2 px-4 rounded"
        >
          Fetch User
        </button>
        <div className="flex">
          <div>{user != null ? <UserCard user={user} /> : ""} </div>
          <div>{user != null ? <Repos user={user} /> : ""}</div>
        </div>
      </div>
    </div>
  );
};

export default Home;
